.carrousel{
    width: 100%;
    height: 280px;
    overflow: hidden;
    position: relative; //for the arrows of slick
    cursor: pointer;
    &-fullHeight{
        height: initial;
    }
    &__img{
        width: 100%;
    }
    &__h2{
        text-align: center;
        margin-top: 5px;
        font-size: 30px;

    }
    &__div{
        height: 250px;
        overflow: hidden;
    }
}

.copyright{
    text-align: center;
    display: block;
    outline: none;
    color: darkgray;
    font-size: 11px;
    padding-top: 10px;
}